<template>
  <div class="goods">
  	<div class="menu-wrapper" ref="menuWrapper">
      <ul>
        <li v-for="(item, index) in goods" :key="item.id" class="menu-item" :class="{'current': currentIndex === index}" @click="selectMenu(index, $event)" ref="menuItem">
          <span class="text border-1px">
            <span v-show="item.type > 0" class="icon" :class="classMap[item.type]"></span>
            {{item.name}}
          </span>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="foodsWrapper">
      <ul>
        <li v-for="item in goods" :key="item.id" class="food-list border-1px" ref="foodList">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li v-for="food in item.foods" class="food-item" :key="food.id" @click="selectFood(food, $event)">
              <div class="icon">
                <img :src="food.icon" width="57" height="57">
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span>月售{{food.sellCount}}份</span>
                  <span>好评率{{food.rating || 0}}%</span>
                </div>
                <div class="price">
                  <span class="now">￥{{food.price}}</span>
                  <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food='food' @goods="_drop"></cartcontrol>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shopcart :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice" :selectFoods = 'selectFoods' ref="shopcart" ></shopcart>
    <food :food="selectedFood" ref="food" @goods='_drop'></food>
  </div>
</template>
<script>
import BetterScroll from 'better-scroll'
import shopcart from 'components/shopcart/shopcart.vue'
import cartcontrol from 'components/cartcontrol/cartcontrol'
import food from 'components/food/food'
// const ERR_OK = 0
export default {
  components: {
    shopcart,
    cartcontrol,
    food
  },
  props: {
    seller: {
      type: Object
    }
  },
  data() {
    return {
      goods: [],
      listHeight: [],
      scrollY: 0,
      selectedFood: {}
    }
  },
  computed: {
    currentIndex() {
      for (let i = 0; i < this.listHeight.length; i++) {
        let height1 = this.listHeight[i]
        let height2 = this.listHeight[i + 1]
        if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
          return i
        }
      }
      return 0
    },
    selectFoods() {
      let foods = []
      this.goods.forEach(good => {
        good.foods.forEach(food => {
          if (food.count) {
            foods.push(food)
          }
        })
      })
      return foods
    }
  },
  created() {
    this.$http.get('../../../static/data.json').then(res => {
      res = res.body
      // if (res.errno === ERR_OK) {
        this.goods = res.goods
        this.$nextTick(() => {
          this._initScroll()
          this._calculateHeight()
        })
      // }
    })
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
  },
  methods: {
    _initScroll() {
      this.meunScroll = new BetterScroll(this.$refs.menuWrapper, {
        click: true
      })
      this.foodsScroll = new BetterScroll(this.$refs.foodsWrapper, {
        click: true,
        probeType: 3
      })
      this.foodsScroll.on('scroll', pos => {
        this.scrollY = Math.abs(Math.round(pos.y))
      })
    },
    _calculateHeight() {
      let foodList = this.$refs.foodList
      let height = 0
      this.listHeight.push(height)
      for (let i = 0; i < foodList.length; i++) {
        let item = foodList[i]
        height += item.clientHeight - 1
        this.listHeight.push(height)
      }
    },
    selectMenu(index, e) {
      if (!e._constructed) {
        return ''
      }
      let foodList = this.$refs.foodList
      let el = foodList[index]
      this.foodsScroll.scrollToElement(el, 300)
    },
    _drop(target) {
      // 动画优化 异步
      this.$nextTick(() => {
        this.$refs.shopcart.drop(target)
      })
    },
    selectFood(food, e) {
      if (!e._constructed) {
        return
      }
      this.selectedFood = food
      this.$refs.food.show()
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '../../common/stylus/mixin.styl'

  .goods{
    display: flex
    position: absolute
    top: 174px
    bottom: 46px
    width: 100%
    overflow hidden
    .menu-wrapper{
      width: 80px
      background: #f3f5f7
      .menu-item{
        display table
        height: 54px
        width: 56px
        line-height 14px
        padding: 0 12px
        &.current{
          position relative
          z-index 100
          margin-top -1px
          background #fff
          font-weight bold
          .text{
            border-none()
          }
        }
        .icon{
          display inline-block
          vertical-align top
          width 12px
          height 12px
          margin-right 4px
          background-size: 12px 12px
          background-repeat: no-repeat
          &.decrease{
            bg-image('decrease_3')
          }
          &.discount{
            bg-image('discount_3')
          }
          &.guarantee{
            bg-image('guarantee_3')
          }
          &.invoice{
            bg-image('invoice_3')
          }
          &.special{
            bg-image('special_3')
          }          
        }
        .text{
          display table-cell
          width 56px
          vertical-align middle
          border-1px(rgba(7,17,27,0.1))
          font-size: 12px
        }
      }
    }
    .foods-wrapper{
      flex: 1
      .title{
        padding-left: 14px
        height 26px
        line-height 26px
        border-left: 2px solid #d9dde1
        font-size 12px
        color: rgb(147,153,159)
        background: #f3f5f7
      }
      .food-item{
        display flex
        margin: 18px
        padding-bottom: 18px
        border-1px(rgba(7, 17, 27, 0.1))
        &:last-child{
          border-none()
          margin-bottom: 0
        }
        .icon{
          width: 57px
          margin-right: 10px
        }
        .content{
          flex: 1
          .name{
            margin: 2px 0 8px 0
            height 14px
            line-height 14px
            font-size 14px
            color rgb(7, 17, 27)
          }
          .desc, .extra{
            line-height 10px
            font-size 10px
            color rgb(147, 153, 159)
          }
          .desc{
            margin-bottom 8px
          }
          .extra{
            .count{
              margin-right 12px
            }
          }
          .price{
            font-weight bold
            line-height 24px
            .now{
              margin-right 8px
              font-size 14px
              color rgb(240, 20, 20)
            }
            .old{
              text-decoration line-through
              font-size 10px
              color rgb(147, 153, 159)
            }
          }
        }
        .cartcontrol-wrapper{
          position: absolute
          right: 0
          bottom: 12px
        }
      }
    }
  }
</style>



