<template>
  <div id="app">
  	<v-header class="header" :seller="seller"></v-header>
  	<div class="tab border-1px">
  	  <div class="tab-item">
  	  	<router-link  to='/goods'>
  	  		商品
  	  	</router-link>
  	  </div>
  	  <div class="tab-item">
  	  	<router-link  to='/ratings'>
  	  		评论
  	  	</router-link>
  	  </div>
  	  <div class="tab-item">
  	    <router-link  to='/seller'>
  	  		商家
  	  	</router-link>
  	  </div>
    </div>
  	<router-view :seller="seller"></router-view>
  </div>
</template>

<script>
import header from 'components/header/header'
export default {
	data() {
		return {
			seller: {}
		}
	},
	created() {
		this.$http.get('../../../static/data.json').then((res) => {
			res = res.body
			// if (res.errno === 0) {
				this.seller = res.seller
			// }
		})
	},
  components: {
  	vHeader: header
  }
}
</script>


<style lang="stylus" scoped>
@import 'common/stylus/mixin.styl'
  .tab{
		display: flex
    width: 100%
    height: 40px
    line-height: 40px
    border-1px(rgba(7,17,27,0.1))
		.tab-item{
			text-align: center
		  flex: 1
			&>a{
				display: block
				font-size 14px
				color rgb(77,85,93)
				&.router-link-active{
					color rgb(240,20,20)
				}
			}
		}
	}
</style>




