<template>
  <div class="cartcontrol">
    <transition name="fade">
      <div class="cart-decrease icon-remove_circle_outline" v-show="food.count > 0" @click.stop="decreaseCart"></div>
    </transition>
    <div class="cart-count" v-show="food.count > 0">{{food.count}}</div>
    <div class="cart-add icon-add_circle" @click.stop="addCart"></div>
  </div>
</template>
<script>
import Vue from 'vue'
export default {
  props: {
    food: {
      type: Object
    }
  },
  created() {
  },
  methods: {
    addCart(e) {
      if (!e._constructed) {
        return ''
      }
      if (!this.food.count) {
        // this.food.count = 1
        Vue.set(this.food, 'count', 1)
      } else {
        this.food.count++
      }
      this.$emit('goods', e.target)
    },
    decreaseCart(e) {
      if (!e._constructed) {
        return ''
      }
      if (this.food.count) {
        this.food.count--
      }
    }
  }
}
</script>
<style lang="stylus" scoped>
  .cartcontrol{
    font-size 0
    .cart-decrease, .cart-add{
      display inline-block
      padding 6px
      font-size 24px
      line-height 24px
      color rgb(0, 160, 220)
      &.fade-enter-active, &.fade-leave-active {
        transition: all .5s;
      }
      &.fade-enter, &.fade-leave-to{
         opacity: 0;
         transform translate3d(24px, 0, 0) rotate(180deg)
      }
    }
    .cart-count{
      display inline-block
      vertical-align top
      width 12px
      padding-top 6px
      line-height 24px
      text-align center
      font-size 10px
      color rgb(147, 153, 159)
    }
  }
</style>



