<template>
  <div class="shopcart">
    <div class="content" @click="toggleList">
      <div class="content-left">
        <div class="logo-wrapper">
          <div class="logo" :class="{'heightlight': totalCount > 0}" ref="logo">
              <i class="icon-shopping_cart" :class="{'heightlight': totalCount > 0}"></i>
          </div>
          <div class="num" v-show="totalCount > 0">{{totalCount}}</div>
        </div>
        <div class="price" :class="{'heightlight': totalPrice > 0}">￥{{totalPrice}}</div>
        <div class="desc">另需配送费￥{{deliveryPrice}}元</div>
      </div>
      <div class="content-right" @click.stop.prevent="pay">
        <div class="pay" :class="payClass">
          {{payDesc}}
        </div>
      </div>
    </div>
    <div class="ball-container">
      <transition-group name="drop" @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter" class="drop">
          <div v-for="(ball, index) in balls" :key="index" class="ball" ref="ball" v-show="ball.show">
            <div class="inner" ref="inner"></div>
          </div>
      </transition-group> 
    </div>
    <transition name="shopcartList">
      <div class="shopcart-list" v-show="listShow()">
        <div class="list-header">
          <h1 class="title">购物车</h1>
          <span class="empty" @click="empty">清空</span>
        </div>
        <div class="list-conter" ref="listConter">
          <ul>
            <li v-for="(food, index) in selectFoods" class="food" :key="index">
              <span class="name">{{food.name}}</span>
              <div class="price">
                <span>￥{{food.price * food.count}}</span>
              </div>
              <div class="cartcontrol-wrapper">
                <cartcontrol :food="food"></cartcontrol> 
              </div>
            </li>
          </ul>
        </div>
      </div>
    </transition>
    <transition name="mask">
      <div class="list-mask" v-show="listShow()" @click="hideList"></div>
    </transition>  
  </div>
</template>
<script>
import cartcontrol from 'components/cartcontrol/cartcontrol' 
import BetterScroll from 'better-scroll'
export default {
  props: {
    deliveryPrice: Number, 
    minPrice: {
      type: Number,
      default: 0
    },
    selectFoods: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      balls: [
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        }
      ],
      dropBalls: [],
      fold: true
    }
  },
  computed: {
    totalPrice() {
      let total = 0
      this.selectFoods.forEach(food => {
        total += food.price * food.count
      })
      return total
    },
    totalCount() {
      let count = 0
      this.selectFoods.forEach(food => {
        count += food.count
      })
      return count
    },
    payDesc() {
      if (this.totalPrice === 0) {
        return `￥${this.minPrice}元起送`
      } else if (this.totalPrice < this.minPrice) {
        let diff = this.minPrice - this.totalPrice
        return `还差￥${diff}元起送`
      } else {
        return '去结算'
      }
    },
    payClass() {
      if (this.totalPrice < this.minPrice) {
        return 'not-enough'
      } else {
        return 'enough'
      }
    }
  },
  methods: {
    listShow() {
      if (!this.totalCount) {
        this.fold = true
        return false
      }
      let show = !this.fold
      if (show) {
        this.$nextTick(() => {
          if (!this.scroll) {
            let el = this.$refs.listConter
            this.scroll = new BetterScroll(el, {
              click: true
            })
          } else {
            this.scroll.refresh()
          }
        })
      }
      return show
    },
    drop(el) {
      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i]
        if (!ball.show) {
          ball.show = true
          ball.el = el
          this.dropBalls.push(ball)
          return
        }
      }
    },
    beforeEnter(el) {
      let count = this.balls.length
      while (count--) {
        let ball = this.balls[count]
        if (ball.show) {
          let rect = ball.el.getBoundingClientRect()
          // ball.el 为food传过来的dom时 加了transition 避免获取不到
          let x = rect.left - 32
          let y = -(window.innerHeight - rect.top - 22)
          el.style.display = ''
          el.style.transform = `translate3d(0,${y}px,0)`
          el.children[0].style.transform = `translate3d(${x}px, 0, 0)`
        }
      }
    },
    enter(el) {
      // let rf = el.offestHeight
      this.$nextTick(() => {
        el.style.transform = 'translate3d(0, 0, 0)'
        el.children[0].style.transform = 'translate3d(0, 0, 0)'
      })
    },
    afterEnter(el) {
      let ball = this.dropBalls.shift()
      if (ball) {
        ball.show = false
        el.style.display = 'none'
      }
    },
    toggleList() {
      if (!this.totalCount) {
        return
      }
      this.fold = !this.fold
    },
    empty() {
      this.selectFoods.forEach(food => {
        food.count = 0
      })
    },
    hideList() {
      this.fold = true
    },
    pay() {
      if (this.totalPrice < this.minPrice) {
        return
      }
      window.alert(`支付${this.totalPrice}元`)
    }
  },
  components: {
    cartcontrol
  }
}
</script>
<style lang="stylus" scoped>
@import '../../common/stylus/mixin.styl'
  .shopcart{
    position: fixed
    left: 0
    bottom: 0
    width: 100%
    height: 48px
    background: #000
    z-index 40
    .content{
      display: flex
      background: #141d27
      font-size: 0
      .content-left{
        flex: 1
        .logo-wrapper{
          display: inline-block
          position: relative
          top: -10px
          margin: 0 12px
          padding: 6px
          width: 56px
          height: 56px
          box-sizing: border-box
          vertical-align top
          border-radius: 50%
          background: rgb(20, 29, 39)
          .num{
            position: absolute
            top 0px
            right 0px
            width 24px
            height 16px
            line-height 16px
            text-align center
            border-radius 16px
            font-size 9px
            font-weight 700
            color #fff
            background rgb(240,20,20)
            box-shadow 0 4px 8px 0 rgba(0,0,0,0.4)
          }
          .logo{
            width: 100%
            height 100%
            border-radius 50%
            background: #2b343c
            text-align: center
            &.heightlight{
              background rgb(0,160,220)
            }
            .icon-shopping_cart{
              line-height 44px
              font-size: 24px
              color: #80858a
              &.heightlight{
                color #fff
              }
            }
          }
        }
        .price{
          display: inline-block
          vertical-align top
          line-height 24px
          margin-top: 12px
          box-sizing border-box
          padding-right: 12px
          border-right: 1px solid rgba(255,255,255,0.1)
          font-size: 16px
          font-weight bold
          color: rgba(255,255,255,0.4)
          &.heightlight{
            color: #fff
          }
        }
        .desc{
          display: inline-block
          line-height 24px
          vertical-align top
          margin: 12px 0 0 12px
          font-size: 16px
          color: rgba(255,255,255,0.4)
        }
      }
      .content-right{
        width: 105px
        .pay{
          height: 48px
          line-height 48px
          text-align: center
          font-size: 12px
          color: rgba(255,255,255, 0.4)
          font-weight bold
          background: #2b333b
          &.not-enough{
            background: #2b333b
          }
          &.enough{
            background: #00b43c
            color: #fff
          }
        }
      }

    }
    .ball-container{
    
      .ball, .drop{
        display inline-block
        width 16px
        height 16px
        position: fixed
        left 32px
        bottom: 22px
        z-index: 200
        &.drop-enter-active{
         transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
        }
        .inner{
            width 16px
            height 16px
            border-radius: 50%
            background: rgb(0, 160, 220)
            transition: all 0.4s linear
        }
      }
    }
    .shopcart-list{
      position: absolute
      left: 0
      bottom: 49px
      z-index: -1
      width 100%
      &.shopcartList-enter-active,
      &.shopcartList-leave-active{
        transition: all 0.5s
      }
      &.shopcartList-enter,
      &.shopcartList-leave-to{
        transform: translate3d(0, 100%, 0)
      }
      .list-header{
        height 40px
        line-height 40px
        padding: 0 18px
        background: #f3f5f7
        border-bottom: 1px solid rgba(7,17,27.0.1)
        .title{
          float: left
          font-size: 14px
          color: rgb(7,17,27)
        }
        .empty{
          float: right
          font-size: 12px
          color: rgb(0,160,220)
        }
      }
      .list-conter{
        padding: 0 18px
        max-height: 217px
        overflow hidden
        background: #fff
        .food{
          position: relative
          padding: 12px 0
          box-sizing: border-box
          border-1px(rgba(7,17,27,0.1))
          .name{
            line-height 24px
            font-size 14px
            color: rgb(7,17,27)
          }
          .price{
            position: absolute
            right: 90px
            bottom: 12px
            line-height: 24px
            font-size: 14px
            font-weight: 700
            color: rgb(240,20,20)
          }
          .cartcontrol-wrapper{
            position: absolute
            right: 0
            bottom: 6px
          }
        }
      }
    }
    .list-mask{
      position: fixed
      top: 0
      left: 0
      bottom: 0
      right: 0
      width 100%
      height: 100%
      z-index: -2
      background: rgba(7,17,27,0.6)
      &.mask-enter-active,
      &.mask-leave-active{
        transition: all 0.5s
      }
      &.mask-enter, &.mask-leave-to{
        opacity: 0
        background: rgba(7,17,27,0)
      }
    }
  }

</style>



